* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:before, :after {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  background: #ffe600;
}

.skin {
  position: relative;
}

.nose {
  width: 0;
  height: 0;
  z-index: 2;
  border: 10px solid #0000;
  border-top-color: #000;
  border-bottom: none;
  margin-left: -10px;
  position: relative;
  top: 150px;
  left: 50%;
}

@keyframes wave {
  0% {
    transform: rotate(0);
  }

  33% {
    transform: rotate(6deg);
  }

  66% {
    transform: rotate(-6deg);
  }

  100% {
    transform: rotate(0);
  }
}

.nose:hover {
  transform-origin: bottom;
  animation: .18s linear infinite wave;
}

