  background: #2e2e2e;
  border: 2px solid #000;
  border-radius: 50%;
  margin-left: -32px;
  position: absolute;
  top: 100px;
  left: 50%;
}

.eye:before {
  content: "";
  width: 30px;
  height: 30px;
  background: #fff;
  border: 3px solid #000;
